.cover-page {
    height: 100vh;
    /* background: pink; */
  }
  
  .navDark {
    background: #D0CBC6 ;
  }
  
  
  .nav-link {
    color: #3F3D3B !important;
  }
  
  .nav-link.active,
  .nav-link:hover {
    color: #a221c9 !important;
  }
  
  .navbar-brand:hover {
    color: #D75721 !important;
  }
  
  .subComponent {
    padding: 50px 0px;
  }
  
  .subComponent-lg {
    padding: 80px 0px;
  }
  
  #hero {
    background: url("../../Assests/bg.gif") center center;
    background-size: cover;
    background-attachment: fixed;
    color: white;
  }
  
  #hero h1 {
    font-family: "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 750;
    font-size: 100px ;
  }
  
  #packageBody {
    background-color: rgb(245, 245, 245);
  }
  .wrapped-page {
    width: 100%;
    height: 100vh;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    background: rgba(0, 0, 0, 0.3);
  }
  
  .item-center {
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
  }
  
  
  
  @media (min-width: 284px) {
    .card-columns {
      margin: 0px 20px;
    }
  
    .tour-msg {
      margin: 0px 10px;
    }
  }
  
  @media (min-width: 576px) {
    .card-columns {
      -webkit-column-count: 2 !important;
      -moz-column-count: 2 !important;
      column-count: 2 !important;
    }
  
    .tour-msg {
      margin: 0px 20px;
    }
  }
  
  @media (min-width: 986px) {
    .card-columns {
      -webkit-column-count: 3 !important;
      -moz-column-count: 3 !important;
      column-count: 3 !important;
    }
  
  
  }
  
  
  .tour-msg input,
  .tour-msg textarea {
    width: 100%;
    margin: 5px;
    border: 1px solid #6c757d;
    padding: 10px;
    border-radius: 5px;
    background: none;
  }
  
  .msg input,
  .msg textarea {
    width: 100%;
    margin: 5px;
    border: 2px solid rgba(255, 255, 255, 0.7);
    padding: 10px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.3);
  }
  
  .msg .btn {
    margin: 5px;
  }
  
  #aboutBody {
    background: #f1f1f1;
    color: #2b2b2b;
  }
  
  .svg-group p {
    margin: 15px 0px;
  }
  
  .svg-card {
    background: #937A9F;
    color: #2b2b2b;
    padding: 30px;
    margin: 10px;
    width: 200px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 25%;
  }
  
  .svg-card:hover {
    border-radius: 50%;
  }
  
  .wrapped-services {
    background: url("../../Assests/banner.gif") center center;
    background-size: cover;
    background-attachment: fixed;
  }
  
  #servicesBody {
    background: rgba(0, 0, 0, 0.7);
  }
  
  #contactBody {
    background: #BD7581;
    color: rgba(0, 0, 0, 0.7);
  }
  
  #footerBody {
    background: #D75721;
    color: rgba(0, 0, 0, 0.7);
  }
  
  #footerBody a {
    color: rgba(0, 0, 0, 0.6);
  }
  
  #footerBody a:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  
  .svg-card-2 {
    color: #ffffff;
    padding: 30px;
    margin: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .svg-card-2:hover {
    border-style: solid;
    border-width: 2px;
    border-color: #e0e0e0;
    background: rgba(255, 255, 255, 0.3);
  }
  
  .svg-card-3 {
    padding: 30px;
    margin: 10px;
    color: rgba(255, 255, 255, 0.7);
  }
  
  .headerTitle p {
    color: rgba(0, 0, 0, 0.5);
  }
  
  * {
    transition-duration: 0.3s;
  }
  
  #home {
    width: 100%;
    height: 100vh;
    scroll-behavior: smooth;
    overflow-y: scroll;
  }
  
  .btn-global{
    background-color: #D75721 !important;
    margin-right: 0.5em !important;
    border: none !important;
  }